JP.JamfNowPlanScreen = function (section) {

  var JAMF_NOW_MIN = 1;
  var JAMF_NOW_MAX = 99999;

  var JamfNowPlanSummaryPanel = JP.JamfNowPlanSummaryPanel();
  var JamfNowPlanMonthlyOnlySummaryPanel = JP.JamfNowPlanMonthlyOnlySummaryPanel();
  var QuantityStepper = JP.QuantityStepper();

  var JamfNowPlanner = React.createClass({
    displayName: 'JamfNowPlanner',

    // Utility to make updating nested selection objects easier
    // @param selectionChanges: { jumpstart_active: true, ios: { active: true, quantity: 60 }}
    // Other selection fields will not be overwritten
    setSelectionState: function (selectionChanges, cb) {
      var clonedSelections = _.clone(this.state.selections);
      var mergedSelections = _.merge(clonedSelections, selectionChanges);
      this.setState({
        selections: mergedSelections
      }, cb);
    },

    componentDidMount: function () {
      var self = this;

      // Trigger a refresh of the exchange rate if necessary
      JP.getExchangeRate();

      // Get priceList, check for saved selections, and calculate totals
      JP.getCurrentPriceList(function (priceList) {
        var selections = JP.savedProductSelections.get(true) || self.state.selections;
        var totals = JP.calculateTotals(selections, true);
        self.setState({
          selections: selections,
          totals: totals,
          priceList: priceList,
          loadingPrices: false
        });
      });
    },

    componentDidUpdate: function (prevProps, prevState) {
      if (prevState.selections !== this.state.selections) {
        // Whenever selections change, save to localStorage and update totals
        JP.savedProductSelections.set(this.state.selections, true);
        this.setState({
          totals: JP.calculateTotals(this.state.selections, true)
        });
      }
    },

    getInitialState: function () {
      // We can't calculate totals until we get the pricelist back, so we start with empty/default state
      // See componentDidMount handler for delayed state updates
      // Loading flag prevents UI from rendering until we're ready
      return {
        replaceLicensePackWithButtons: JP.getViewConfig('jamf-now-plan').options.replaceLicensePackWithButtons,
        onlySummarizeMonthlyCosts: JP.getViewConfig('jamf-now-plan').options.onlySummarizeMonthlyCosts,
        loading: true,
        selections: {
          jamf_now: {active: false, quantity: 50},
          support: "1_year",
          license: "subscription",
        },
        totals: {
          software_and_support_total: null,
          discount_total: null,
          jumpstart_total: null,
          training_pass_total: null,
          overall_total: null,
          renewals: {},
        },
        priceList: null
      }
    },
    jamfNowToggled: function (forceActiveState) {
      this.setSelectionState({
        'jamf_now': {
          active: forceActiveState || !this.state.selections.jamf_now.active,
          quantity: this.state.selections.jamf_now.quantity
        }
      })
    },
    jamfNowQuantityInputChanged: function (newQuantity) {
      this.setSelectionState({
        'jamf_now': {active: true, quantity: newQuantity}
      });
    },
    jamfNowEnforceQuantityBounds: function (quantity) {
      quantity = Math.min(JAMF_NOW_MAX, quantity);
      quantity = Math.max(JAMF_NOW_MIN, quantity);
      return quantity;
    },
    jamfNowLabelClicked: function () {
      this.jamfNowToggled(false);
    },
    oneYearSupportClicked: function () {
      this.setSelectionState({
        support: '1_year'
      });
    },
    twoYearSupportClicked: function () {
      this.setSelectionState({
        support: '2_year'
      })
    },
    activeClassWhenTrue: function (bool) {
      return bool ? ' active' : '';
    },
    disabledClassWhenNoMainProducts: function () {
      return this.noMainProducts() ? ' disabled' : '';
    },
    noMainProducts: function () {
      return !this.state.selections.jamf_now.active;
    },
    render: function () {

      var lowerPanel = this.state.replaceLicensePackWithButtons.enabled ?
        <div className="row row-cta-buttons">
          <div className="col-md-6">
            <a className="btn btn-green btn-large" href={this.state.replaceLicensePackWithButtons.button1.href}
               title={this.state.replaceLicensePackWithButtons.button1.title}
               target="_blank">{this.state.replaceLicensePackWithButtons.button1.label}</a>
          </div>
          <div className="col-md-6">
            <a className="btn btn-blue btn-large" href={this.state.replaceLicensePackWithButtons.button2.href}
               title={this.state.replaceLicensePackWithButtons.button2.title}
               target="_blank">{this.state.replaceLicensePackWithButtons.button2.label}</a>
          </div>
        </div>
        :
        <div className={this.disabledClassWhenNoMainProducts()}>
          <div className="row row-buttons">
            <div className="col-md-12">
              <h4>{i18n.properties.jamf_now_plan.license_pack_term}</h4>
              <div className="btn-group btn-group-justified" role="group">
                <a className={'btn' + this.activeClassWhenTrue(this.state.selections.support === '1_year')}
                   onClick={this.oneYearSupportClicked}> 1 {i18n.properties.pricing.support.year} </a>
                <a className={'btn' + this.activeClassWhenTrue(this.state.selections.support === '2_year')}
                   onClick={this.twoYearSupportClicked}>
                  2 {i18n.properties.pricing.support.years}
                </a>
              </div>
            </div>
          </div>
        </div>;

      var summaryPanel = this.state.onlySummarizeMonthlyCosts ?
        <JamfNowPlanMonthlyOnlySummaryPanel showInstructions={this.noMainProducts()}
                                            renewals={this.state.totals.renewals}/>
        :
        <JamfNowPlanSummaryPanel software_and_support_total={this.state.totals.software_and_support_total}
                                 discount_total={this.state.totals.discount_total}
                                 jumpstart_total={this.state.totals.jumpstart_total}
                                 training_pass_total={this.state.totals.training_pass_total}
                                 overall_total={this.state.totals.overall_total}
                                 support={this.state.selections.support}
                                 showInstructions={this.noMainProducts()}
                                 renewals={this.state.totals.renewals}/>

      return (
        <div className={'row row-flex' + (this.state.loadingPrices ? ' loading' : '')}>
          <div className="col-md-7">
            <div className="row">
              <div className="col-md-12">
                <h4>{i18n.properties.jamf_now_plan.device_count}</h4>
                <div
                  className={'btn btn-labeled-quantity-picker' + this.activeClassWhenTrue(this.state.selections.jamf_now.active)}
                  onClick={this.jamfNowLabelClicked}>
                  <div className="label">
                    <strong>{i18n.properties.jamf_now_plan.macOS_and_iOS}</strong>
                    {i18n.properties.jamf_now_plan.first_3_devices_free}
                  </div>
                  <div className="picker">
                    <QuantityStepper step={10}
                                     quantity={this.state.selections.jamf_now.quantity}
                                     quantityChanged={this.jamfNowQuantityInputChanged}
                                     toggleProduct={this.jamfNowToggled}
                                     enforceQuantityBounds={this.jamfNowEnforceQuantityBounds}/>
                  </div>
                </div>
              </div>
            </div>
            {lowerPanel}
          </div>
          {summaryPanel}
        </div>
      )
    }
  });

  ReactDOM.render(<JamfNowPlanner/>, section.get(0));
};;
