JP.olarkInit = function () {

  var chatWindowOpen = false;
  var chatWindowReady = false;
  var chatToggleItem = $('#chat-toggle-item');
  var chatToggle = $('#chat-toggle');

  olark.configure('system.is_single_page_application', true);
  olark.configure('system.chat_does_not_follow_external_links', true);
  olark.configure('box.start_expanded', false);

  if (JP.config.chat.operatorGroup.enabled) {
    olark.configure('system.group', JP.config.chat.operatorGroup.id);
  }

  chatToggleItem.addClass('enabled');

  chatToggle.on('click', function () {
    if (!chatWindowOpen) {
      // Showing it will also expand
      olark('api.box.show');
      return;
    }

    // Just shrink, the onShrink handle will actually close it
    olark('api.box.shrink');
    chatToggleItem.removeClass('active');
  });

  olark('api.box.onShrink', function (event) {
    // Automatically hide when shrunk
    window.setTimeout(function () {
      olark('api.box.hide');
    }, 500);
  });

  olark('api.box.onHide', function (event) {
    chatWindowOpen = false;
    chatToggleItem.removeClass('active');
  });

  olark('api.box.onShow', function (event) {
    // Olark tries to start with the window open, regardless of box.start_expanded setting
    // So DON'T follow-on this initial show command with the expand command we usually use
    if (chatWindowReady) {
      chatWindowOpen = true;
      chatToggleItem.addClass('active');

      // Automatically expand when shown
      window.setTimeout(function () {
        olark('api.box.expand');
      });
    }
  });

  olark('api.chat.onReady', function (event) {
    chatWindowReady = true;
    // Short-circuit silly open-window-on-load behavior
    olark('api.box.shrink');
    olark('api.box.hide');
  });
};