
JP.OverviewScreen = function (section) {
  var config = JP.getViewConfig('overview');
  var SixTileGrid = JP.SixTileGrid();
  var OverviewTiles = React.createClass({
    displayName: 'OverviewTiles',
    render: function () {
      return <SixTileGrid tiles={config.options.tiles}></SixTileGrid>
    }
  });

  ReactDOM.render(
    <OverviewTiles />,
    section.get(0)
  );
};;
