
JP.JamfProAppleAtWorkScreen = function (section) {
  var config = JP.getViewConfig('jamf-pro-apple-at-work');
  var SixTileGrid = JP.SixTileGrid();
  
  var JamfProAppleAtWorkScreen = React.createClass({
    displayName: 'JamfProAppleAtWorkScreen',
    render: function () {
      return (<SixTileGrid tiles={config.options.tiles}></SixTileGrid>)
    }
  });

  ReactDOM.render(
    <JamfProAppleAtWorkScreen />,
    section.get(0)
  );
};;
