JP.getQuantity = function(productId, selections) {

  // Some product values are simple booleans representing "selected or not"
  if (selections[productId] === true){
    return 1;
  }

  // Some product values are objects, like { active: true, quantity: 50 }
  if (selections[productId] && selections[productId]['active']) {
    return parseInt(selections[productId]['quantity']);
  }

  return 0;
};
