JP.savedProductSelections = {
  get: function (isJamfNow){
    return JSON.parse(localStorage.getItem(this.lsKey(isJamfNow)));
  },
  set: function(productSelections, isJamfNow){
    localStorage.setItem(this.lsKey(isJamfNow), JSON.stringify(productSelections));
  },
  clear: function (isJamfNow){
    localStorage.removeItem(this.lsKey(isJamfNow));
  },
  lsKey: function(isJamfNow){
    return isJamfNow ? 'JP_Saved_Jamf_Now_Product_Selections' : 'JP_Saved_Jamf_Pro_Product_Selections';
  }
};