JP.getPremiumServiceCost = function (productSelections, renewals) {
  var annualCost = 0;
  var pricing;
  var selectedServiceProductId;

  _.forEach(['premium_service_silver', 'premium_service_gold', 'premium_service_platinum'], function(serviceProductId){
    if (productSelections[serviceProductId]){
      selectedServiceProductId = serviceProductId;
      return false;
    }
  });

  if (selectedServiceProductId) {
    pricing = _.find(JP.currentPriceList.products, {product: selectedServiceProductId});
    if (pricing && pricing.price) {
      annualCost = parseFloat(pricing.price);
    } else {
      console.error('Error: pricing for ' + selectedServiceProductId + ' not found.');
    }

    if (annualCost){
      renewals.push({
        id: selectedServiceProductId,
        displayName: 'Premium Service',  // TODO Localize
        unitCost: '--',
        totalCost: annualCost
      })
    }
  }

  return annualCost;
};