JP.JamfNowPlanSummaryPanel = function () {

  var PlanSummaryRenewalTable = JP.PlanSummaryRenewalTable();

  return React.createClass({
    displayName: 'JamfNowPlanSummaryPanel',
    getInitialState: function () {
      return {
        useLocalCurrency: JP.useLocalCurrency
      }
    },
    quoteLinkClicked: function (event) {
      JP.quoteDetails.display(true);
    },
    render: function () {
      return (
        <div className="col-md-5 summary-column">
          <h4>{i18n.properties.pricing.summary.title}</h4>
          <div className={'summary-panel' + (this.props.showInstructions ? ' show-instructions' : '')}>
            <p className="summary-instructions">
              {i18n.properties.pricing.summary.instructions}
            </p>
            <div className="summary-totals row">
              <div className="col-sm-6 col-sm-push-6 col-md-12 col-md-push-0">
                <h5 className="summary-panel-header-with-subtitle">
                  { this.props.support === '1_year' ? i18n.properties.pricing.summary.first_year : 'First 2 Years' }
                </h5>
                <dl>
                  <dt>{i18n.properties.pricing.summary.software_and_support}</dt>
                  <dd>{JP.formatAsActiveCurrency(this.props.software_and_support_total || '--')}</dd>
                  <dt>First 3 Devices Free</dt>
                  <dd className="discount">{JP.formatAsActiveCurrency(this.props.discount_total || '--')}</dd>
                </dl>
                <hr></hr>
                <strong className="total">{JP.formatAsActiveCurrency(this.props.overall_total || '--')}</strong>
                <a className="btn btn-quote" onClick={this.quoteLinkClicked}><svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid" width="18" height="23.94" viewBox="0 0 18 23.94"><path d="M17.98 7.098v14.859c0 1.1-.896 1.995-1.998 1.995H1.996a1.999 1.999 0 0 1-1.998-1.995V2.017C-.002.918.894.023 1.996.023h8.991c.046 0 .085.02.129.026a.909.909 0 0 1 .126.026c.172.046.331.12.453.241l5.884 5.873c.032.022.047.06.076.086.029.027.052.058.078.089.154.175.267.387.267.638 0 .034-.016.063-.02.096zm-5.994-3.671v2.578h2.584l-2.584-2.578zm3.996 4.572h-4.995a.998.998 0 0 1-.999-.997V1.97H2l-.004 19.987h13.986V7.999zM3.994 10.99h5.994a.998.998 0 1 1 0 1.994H3.994a.998.998 0 1 1 0-1.994zm0 2.991h5.994a.998.998 0 1 1 0 1.995H3.994a.998.998 0 1 1 0-1.995zm0 2.992h5.994a.997.997 0 1 1 0 1.993H3.994a.998.998 0 1 1 0-1.993zm8.991-5.983h.999a.998.998 0 1 1 0 1.994h-.999a.998.998 0 1 1 0-1.994zm0 2.991h.999a.998.998 0 1 1 0 1.995h-.999a.998.998 0 1 1 0-1.995zm0 2.992h.999a.997.997 0 1 1 0 1.993h-.999a.998.998 0 1 1 0-1.993z"/></svg>
                  Quote
                </a>
              </div>
              <PlanSummaryRenewalTable renewals={this.props.renewals} title={'Monthly Renewal'}
                                       subtitle={'After Term Ends'} columnNames={['Per Device', i18n.properties.pricing.summary.total]}/>
            </div>
          </div>
        </div>
      )
    }
  });
};;
