$(function () {

  // Called on every hash change
  function updateViews() {
    var
      route = window.location.hash.slice(1),
      requestedSection = $('section[data-route="' + route + '"]');

    if (requestedSection.length && !requestedSection.hasClass('active')) {

      // Disable videos on previous screen
      var videoIFrame = $('[data-route].active .youtube-iframe');
      videoIFrame.attr('data-old-src', videoIFrame.attr('src')).attr('src', '');

      // Reset videos on requested screen
      var newVideoIFrame = requestedSection.find('.youtube-iframe');
      newVideoIFrame.attr('src', newVideoIFrame.attr('data-old-src'));

      $('[data-route].active').removeClass('active');
      requestedSection.addClass('active');

      // Nav link activation
      var navLink = $('nav a[href="#' + route + '"]').last();
      if (!navLink.hasClass('active')) {
        navLink.closest('nav').find('.active').removeClass('active');
        navLink.closest('li').addClass('active');

        // Expand/collapse parent nav items if necessary
        $('nav ul.expanded').removeClass('expanded');
        navLink.closest('nav li ul').addClass('expanded').parent('li').addClass('active');
      }

      // Execute init JS specified by section (these are only run once on first view)
      var initFn = requestedSection.data('init');
      if (initFn && initFn.length) {
        if (!JP[initFn]) {
          console.error('The initFn: ' + initFn + ' for #' + route + ' was not found.');
        } else {
          JP[initFn](requestedSection);
          requestedSection.removeData('init').removeAttr('data-init');
        }
      }

      // Issue routeLoaded event
      $(document).trigger('routeLoaded:' + route);

      analytics.page(route);
    }

    if (requestedSection.length === 0) {
      loadDefaultView();
    }
  }

  function loadDefaultView() {
    window.location.hash = '#' + JP.DEFAULT_VIEW;
  }

  JP.configureNav();

  var router = Router({
    '*': updateViews
  });
  router.init();

  if (window.location.hash.length === 0) {
    loadDefaultView();
  }
});