JP.getProductPricingFromPriceList = function (product, support, vertical, license) {

  // Some editions piggyback on other distributors for pricing, per "distributorForPricing"
  var productKey = JP.config.distributorForPricing + "_" + vertical;
  var productData = _.find(JP.currentPriceList.products, {
    "product": product,
    "license": license,
    "support": support
  });

  if (productData && productData[productKey]) {
    return productData[productKey];
  } else {
    console.error('Error: product pricing not found for product: ' + product + ', vertical: ' + vertical + ', license: ' + license + ', support: '
      + support + ' in pricing:', productKey);
    return null;
  }
};