// Modal utility
// @param classToAdd: a CSS class to add to the .modal-message (optional)
// @param autoCloseTimeout: time in millis to wait before automatically closing the modal (optional)
JP.modal = {
  display: function (contentHTML, classToAdd, autoCloseTimeout, disableClickToClose) {
    var
      modalMessage = $('#modalMessage'),
      modalBackdrop = $('.modal-backdrop'),
      both = modalBackdrop.add(modalMessage);

    if (classToAdd) {
      modalMessage.addClass(classToAdd);
    }
    modalMessage.show();

    // Wait for the repaint/layout triggered by the new modal-message class
    window.setTimeout(function(){
      modalBackdrop.addClass('open');
      modalMessage.html(contentHTML).addClass('open');
    }, 0);

    // Close when they click anywhere
    both.on('click', function(){
      if (!disableClickToClose){
        JP.modal.hide()
      }
    });

    if (autoCloseTimeout){
      window.setTimeout(JP.modal.hide, autoCloseTimeout);
    }
  },
  hide: function() {
    var
      modalMessage = $('#modalMessage'),
      modalBackdrop = $('.modal-backdrop'),
      both = modalBackdrop.add(modalMessage);

    both.removeClass('open').off('click');

    // Wait for exit transition before cleaning out added classes
    window.setTimeout(function(){
      modalMessage.attr('class', 'modal-message');
      modalMessage.hide();
    }, 500)
  }
};

JP.initModalTriggerEvents = function(){
  $(document).on('click', '[data-trigger-feature-modal]', function(){
    var button = $(this);
    var modalContentId = button.attr('data-trigger-feature-modal');
    JP.modal.display($('#' + modalContentId).html(), 'modal-feature');
  });

  $(document).on('click', '[data-trigger-screenshot-modal]', function(){
    var button = $(this);
    var modalContentId = button.attr('data-trigger-screenshot-modal');
    JP.modal.display($('#' + modalContentId).html(), 'modal-screenshot');
  });
};
JP.initModalTriggerEvents();


