
JP.DeploymentGuidesScreen = function (section) {
  var config = JP.getViewConfig('deployment-guides');
  var SixTileGrid = JP.SixTileGrid();
  var DeploymentGuidesTiles = React.createClass({
    displayName: 'DeploymentGuidesTiles',
    render: function () {
      return <SixTileGrid tiles={config.options.tiles}></SixTileGrid>
    }
  });

  ReactDOM.render(
    <DeploymentGuidesTiles />,
    section.get(0)
  );
};;
