(function () {
  var LS_KEY = 'JP_Stores';

  function setLocalStorage(stores) {
    localStorage.setItem(LS_KEY, JSON.stringify({
      'stores': stores,
      'timestamp': new Date().getTime()
    }));
  }

  function getFromLocalStorage() {
    return JSON.parse(localStorage.getItem(LS_KEY));
  }

  // Stores considered fresh for 12 hours
  function areFresh(rates) {
    var
      now = new Date(),
      nowInMillis = now.getTime(),
      timestampInMillis = parseInt(rates['timestamp']),
      twelveHoursInMillis = 43200000;
    return timestampInMillis > nowInMillis - twelveHoursInMillis;
  }

  // This function is async and returns a jQuery Deferred object
  // Accepts an optional array of locales to filter on
  JP.getStores = function (locales) {
    var deferred = $.Deferred();
    var savedStores = getFromLocalStorage();

    if (savedStores && areFresh(savedStores)) {
      deferred.resolve(savedStores.stores);
    } else {
      console.info('Stores missing or out of date.  Refreshing now.');

      console.log(' JP.getStores > locales', locales);
      var url = JP.envConfig.apiUrl + "stores.json";
      if (locales && locales.length) {
        url += "?locale=" + locales.join(',')
      }

      $.getJSON(url)
        .done(function (data) {
          setLocalStorage(data.stores);
          deferred.resolve(data.stores);
        }).fail(function (error) {
        console.error('Error fetching rates: ', error);
        deferred.reject(error);
      });
    }
    return deferred;
  };
})();

