JP.getCurrentPriceList = function (callback) {
  var LS_KEY = 'JP_PriceList';

  function convertQuantityDiscountBreakpoints(seatsPricingLegend) {
    // from  ["1_249", "250_999", "1000_2499", "2500_4999", "5000_9999", "10000_plus"]
    // to    [250, 1000, 2500, 5000, 10000, 999999]
    var quantityDiscountBreakpoints = [];
    for (var i = 1; i < seatsPricingLegend.length; i++) {
      var split = seatsPricingLegend[i].split('_');
      quantityDiscountBreakpoints.push(split[0]);
    }
    // add a max value
    quantityDiscountBreakpoints.push(9999999);
    return quantityDiscountBreakpoints;
  }

  function setLocalStorage(priceList) {
    localStorage.setItem(LS_KEY, JSON.stringify({
      'products': priceList.products,
      'timestamp': new Date().getTime(),
      'quantityDiscountBreakpoints': priceList.quantityDiscountBreakpoints
    }));
  }

  function getFromLocalStorage() {
    return JSON.parse(localStorage.getItem(LS_KEY));
  }

  // Rates considered fresh for 12 hours
  function isFresh(rates) {
    var
      now = new Date(),
      nowInMillis = now.getTime(),
      timestampInMillis = parseInt(rates['timestamp']),
      twelveHoursInMillis = 43200000;
    return timestampInMillis > nowInMillis - twelveHoursInMillis;
  }

  var savedPriceList = getFromLocalStorage();

  if (savedPriceList && isFresh(savedPriceList)) {
    JP.currentPriceList = savedPriceList;
    callback(savedPriceList);
  } else {
    console.info('PriceList missing or out of date.  Refreshing now.');
    $.getJSON(JP.envConfig.apiUrl + 'products.json')
      .done(function (priceList) {
        JP.currentPriceList = priceList;
        JP.currentPriceList.quantityDiscountBreakpoints = convertQuantityDiscountBreakpoints(priceList.seats_pricing_legend);
        setLocalStorage(priceList);
        callback(priceList);
      })
      .fail(function (error) {
        console.error('Error fetching PriceList: ', error);
      });
    return false;
  }
};
