// Formats raw number in dollars as string with active currency symbol, delimiters and exchange rate
// Adapted from http://stackoverflow.com/a/9318724
JP.formatAsActiveCurrency = function (n) {
  var
    decPlaces = n % 1 === 0 ? 0 : 2, // Show decimals if it's not a whole number
    decimalDelimiter = i18n.properties.decimalDelimiter,
    thousandsDelimiter = i18n.properties.thousandsDelimiter,
    currencySymbol = JP.useLocalCurrency ? i18n.properties.currencySymbol : "$",
    prefix = '',
    suffix = '',
    i, j;

  // Exception for intentionally blank value
  if (n === '--') {
    return n;
  }

  if (JP.useLocalCurrency) {
    n = n * JP.getExchangeRate();
  }

  if (n < 0) {
    prefix = '(';
    suffix = ')';
  }

  i = parseInt(n = Math.abs(+n || 0).toFixed(decPlaces)) + "";
  j = (j = i.length) > 3 ? j % 3 : 0;

  return prefix + currencySymbol + (j ? i.substr(0, j) + thousandsDelimiter : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + thousandsDelimiter) + (decPlaces ? decimalDelimiter + Math.abs(n - i).toFixed(decPlaces).slice(2) : "") + suffix;
};