JP.initGetStartedEnterpriseScreen = function (section) {

  var viewConfig = JP.getViewConfig('get-started-enterprise');
  var marketoFormReadyRun = false;
  var STORES = [];  // To be populated from API later
  var COUNTRIES = ["Australia", "Belgium", "Canada", "China", "Denmark", "Finland", "France", "Germany", "Hong Kong", "India", "Japan", "Netherlands", "Norway", "Sweden", "Switzerland", "UK", "US", "United Arab Emirates"];
  var US_STATES = ["AL", "AK", "AS", "AZ", "AR", "CA", "CO", "CT", "DE", "DC", "FM", "FL", "GA", "GU", "HI", "ID", "IL", "IN", "IA", "KS", "KY", "LA", "ME", "MH", "MD", "MA", "MI", "MN", "MS", "MO", "MT", "NE", "NV", "NH", "NJ", "NM", "NY", "NC", "ND", "MP", "OH", "OK", "OR", "PW", "PA", "PR", "RI", "SC", "SD", "TN", "TX", "UT", "VT", "VI", "VA", "WA", "WV", "WI", "WY"];
  var CA_PROVICES = ["Alberta", "British Columbia", "Manitoba", "New Brunswick", "Newfoundland and Labrador", "Nova Scotia", "Nunavut", "Northwest Territories", "Ontario", "Prince Edward Island", "Quebec", "Saskatchewan", "Yukon"];

  function getTextField(fieldName) {
    return $('<input id="' + fieldName + '" name="' + fieldName + '" maxlength="255" type="text" class="form-control" tabindex="1">');
  }

  function getSelect(fieldName) {
    return $('<select id="' + fieldName + '" name="' + fieldName + '" class="form-control" tabindex="1"></select>')
  }

  function getOption(optionValue) {
    return $('<option value="' + optionValue + '">' + optionValue + '</option>');
  }

  function getRow(fieldName, labelText) {
    return $('<div class="mktoFormRow"><label for="' + fieldName + '">' + labelText + '</label></div>');
  }

  function createSectionsAndHeaders() {
    section.find('.mktoFormRow').slice(0, 3).wrapAll('<div class="col-md-6 reseller"></div>').first().before('<h3>' + i18n.properties.get_started.you.title + '</h3>');
    section.find('.mktoFormRow').slice(3, 20).wrapAll('<div class="col-md-6 customer"></div>').first().before('<h3>' + i18n.properties.get_started.customer.title + '</h3>');
  }

  function splitResellerIntoFirstNameLastName() {
    // Front the reseller name field with separate FirstName and LastName fields that combine to form one name
    var
      resellerNameField = section.find('input[name=Reseller_Contact_Name__c]'),
      resellerNameRow = resellerNameField.closest('.mktoFormRow'),
      firstNameField = getTextField('ResellerFirstName'),
      firstNameRow = getRow('ResellerFirstName', i18n.properties.get_started.you.first_name).append(firstNameField),
      lastNameField = getTextField('ResellerLastName'),
      lastNameRow = getRow('ResellerLasttName', i18n.properties.get_started.you.last_name).append(lastNameField);

    // Combine firstName and lastName
    firstNameRow.add(lastNameRow).find('input').on('change', function () {
      resellerNameField.val(firstNameField.val() + ' ' + lastNameField.val());
    });

    // Squish this field in such a way hide it while still allowing it's validation message to display
    resellerNameRow.css({
      marginBottom: 0,
      marginTop: '-10px'
    });
    resellerNameRow.find('label').hide();
    resellerNameRow.find('input').css({
      zIndex: -1,
      position: 'absolute',
      opacity: 0
    }).attr('tabindex', 0);
    resellerNameField.on('focus', function () {
      firstNameField.trigger('focus');
    });
    resellerNameRow.before(firstNameRow);
    resellerNameRow.before(lastNameRow);
  }

  function prepopulateHiddenPricingAndNumberOfDevices() {
    var storedPricing = JP.savedProductSelections.get(false);
    if (storedPricing) {
      section.find('[name=Goals__c]').val(JP.prettifyPricing(storedPricing));

      var totalDevices = 0;
      _.each(['macos', 'ios'], function (productId) {
        if (storedPricing[productId] && storedPricing[productId]['active']) {
          totalDevices += parseInt(storedPricing[productId]['value']);
        }
      });
      if (totalDevices > 0) {
        section.find('#numberofAppleDevices').val(totalDevices);
      }
    }
  }

  function addLocalizedCountryStateStoreDropdowns() {
    var
      resellerEmailField = section.find('#Reseller_Contact_Email__c'),

      // Reseller country
      resellerCountryDropdown = getSelect('ResellerCountry'),
      resellerCountryRow = getRow('ResellerCountry', i18n.properties.get_started.you.country).append(resellerCountryDropdown),

      // Customer country/state
      customerCountryDropdown = getSelect('Country'),
      customerCountryRow = getRow('Country', i18n.properties.get_started.customer.country).append(customerCountryDropdown),
      customerStateDropdown = getSelect('State'),
      customerStateRow = getRow('State', i18n.properties.get_started.you.state).append(customerStateDropdown).hide();

    resellerCountryDropdown.attr('required', 'required');
    customerCountryDropdown.attr('required', 'required');

    function setCountries(countryDropdown) {
      var countryOptions = _.map(COUNTRIES, function (country) {
        return getOption(country);
      });
      // Add empty option
      countryOptions.unshift('<option value="" selected disabled>Select...</option>');
      countryDropdown.html(countryOptions);
    }

    // Set the correct options (or hide the state row if relevant)
    function updateStateRow(stateDropdown, stateRow, selectedCountry) {
      var states = [];
      var stateOptions;

      if (selectedCountry === 'US') {
        states = US_STATES;
      } else if (selectedCountry === 'Canada') {
        states = CA_PROVICES;
      }

      if (states.length <= 1) {
        stateRow.slideUp();
        stateDropdown.removeAttr('required');
      } else {
        stateOptions = _.map(states, getOption);
        stateOptions.unshift('<option value="" selected disabled>Select...</option>');
        stateDropdown.attr('required', 'required');
        stateRow.slideDown()
      }
      stateDropdown.html(stateOptions);
    }

    setCountries(resellerCountryDropdown);

    // When change the reseller country is changed, update the customer country if it's empty
    resellerCountryDropdown.on('change', function(){
      customerCountryDropdown.val(resellerCountryDropdown.val()).trigger('change');
    });

    setCountries(customerCountryDropdown);
    updateStateRow(customerStateDropdown, customerStateRow, customerCountryDropdown.val());
    customerCountryDropdown.on('change', function () {
      updateStateRow(customerStateDropdown, customerStateRow, customerCountryDropdown.val());
    });
    
    // Insert new rows!  (These rows are NOT present in the generated marketo form!)
    resellerEmailField.closest('.mktoFormRow').after([resellerCountryRow]);
    $('[name=numberofAppleDevices]').closest('.mktoFormRow').after([customerCountryRow, customerStateRow]);
  }

  function addLocationRow(){
    var locationRow = getRow('Reseller_Location', 'Your Location');
    var locationDropdown = getSelect('Reseller_Location');
    locationDropdown.prop('required', true);
    locationRow.append(locationDropdown);
    locationDropdown.append([
      '<option value="" selected disabled>Select...</option>',
      getOption('Inside'),
      getOption('Field')
    ]);
    $('[name=ResellerCountry]').closest('.mktoFormRow').after(locationRow);
  }

  function saveFormInput() {
    // We save all of this info, but it is not always used.
    // We only reinstate name and email when Online has been selected
    var input = {};
    _.each(['ResellerCountry', 'Reseller_Contact_Email__c', 'Reseller_Location', 'Country', 'State'], function(fieldName){
      input[fieldName] = $('#' + fieldName).val();
    });
    localStorage.setItem('JP_GetStartedEnterpriseFormInput', JSON.stringify(input));
  }

  function getSavedFormInput() {
    return JSON.parse(localStorage.getItem('JP_GetStartedEnterpriseFormInput'));
  }

  function selectSavedOption(dropdown, dataKey) {
    var savedInfo = getSavedFormInput();
    if (savedInfo && savedInfo[dataKey]) {
      dropdown.find('option[value="' + savedInfo[dataKey] + '"]').attr('selected', 'selected');
    }
  }

  function reconstituteSavedFormInput(){
    _.each(getSavedFormInput(), function(value, fieldName){
      var elem = $('#' + fieldName);
      if (elem.is('select')){
        elem.find('option[value="' + value + '"]').attr('selected', 'selected');
      } else if (elem.is('input')){
        elem.val(value);
      }
    });
  }

  function callMarketoNewForm(formDescriptor) {
    MktoForms2.newForm(formDescriptor, function (form) {
      var lpFields = {
        "lpId": 1605,
        "subId": 16,
        "munchkinId": "704-VYP-527"
      };
      var pageFields = MktoForms2.getPageFields();
      form.addHiddenFields(lpFields);
      form.addHiddenFields(pageFields);
      if (!form.EnableDeferredMode) {
        form.render();
      }
      //Validate form
      form.onSubmit(function () {

        if (form.validate()) {
          form.submitable(true);
        }
        else {
          form.submitable(false);
        }
      });

      form.onSuccess(function (values, followUpUrl) {
        JP.modal.display('<p>Thank you for reaching out.</p> <p>We will be in touch shortly with next steps.</p>', 'success', 5000);
        JP.savedProductSelections.clear(false);
        section.find('button[type=submit]').text(i18n.properties.get_started.customer.contact_us).removeAttr('disabled');

        // Save certain form input values to localStorage
        saveFormInput();

        // Clear frequently changed fields
        section.find('#ResellerFirstName, #ResellerLastName, #Internal_Sales_Notes__c, #FirstName, #LastName, #Email, ' +
          '#Company, #numberofAppleDevices, #Industry').filter(':visible').val('');

        return false;
      });
    });
  }

  // Get store list, then build the form
  JP.getStores().then(function (stores) {
    STORES = stores;

    JP.getFormDescriptorForGetStartedEnterpriseForm(viewConfig.options.marketoFormId)
      .then(function (customFormDescriptor) {

        // Set the ID for the form itself
        section.find('form').attr('id', "mktoForm_" + viewConfig.options.marketoFormId);

        // Trigger marketo's form builder
        callMarketoNewForm(customFormDescriptor);

        // Do around of customization
        MktoForms2.whenReady(function () {
          var form;
          if (!marketoFormReadyRun) {
            marketoFormReadyRun = true;
            form = section.find('form');
            JP.marketoUtils.cleanUpMarkup(form);
            createSectionsAndHeaders();
            addLocalizedCountryStateStoreDropdowns();
            addLocationRow();
            splitResellerIntoFirstNameLastName();
            prepopulateHiddenPricingAndNumberOfDevices();
            reconstituteSavedFormInput();

            // Show the form, after a pause for show/hide animations to complete
            setTimeout(function () {
              form.attr('style', '').show();
            }, 320);

            // Auto-focus in iOS 8 is problematic: http://stackoverflow.com/questions/26146252/
            if (!(/(iPad|iPhone|iPod)/g.test(navigator.userAgent))) {
              getTextField('ResellerFirstName').trigger('focus');
            }
          }
        });
      }, function (error) {
        console.error(error);
      });
  });
};
