// Common utility for setting up video screens - works for Jamf Now as well as Jamf Pro
JP.initVideos = function (section) {
  
  // If there are overrides configured for the standard localized videos, apply those first
  // Ultimately, all videos will be handled in configs, but that requires that the configs be locked to a single locale
  function applyOverride(videoConfig, videoNumber) {
    console.log('applyOverride', videoConfig, videoNumber);
    var thumbItem = section.find('.thumbs li:nth-child(' + videoNumber + ')');
    thumbItem.find('a').attr('href', 'https://www.youtube.com/embed/' + videoConfig.id + '?modestbranding=1&rel=0&wmode=opaque');
    thumbItem.find('img').attr('src', videoConfig.imgUrl).attr('alt', videoConfig.label);
    thumbItem.find('.caption').text(videoConfig.label);
  }

  var config = JP.getViewConfig(section.attr('data-route'));
  if (config.options.overrideVideo1.enabled) {
    applyOverride(config.options.overrideVideo1, 1);
  }
  if (config.options.overrideVideo2.enabled) {
    applyOverride(config.options.overrideVideo2, 2);
  }
  if (config.options.overrideVideo3.enabled) {
    applyOverride(config.options.overrideVideo3, 3);
  }

  section.find('iframe').attr('src', section.find('.playlist li.active a').attr('href'));
  section.find('.playlist .thumbs a').on("click", function () {
      $(this).closest('.playlist').find('iframe').attr('src', $(this).attr("href"));
      $(this).parent('li').parent('ul').children('li').removeClass('active');
      $(this).parent('li').addClass('active');
      return false;
    }
  );
  $('.fitVids').fitVids();
};