
JP.JamfProOverviewScreen = function (section) {
  var config = JP.getViewConfig('jamf-pro-overview');
  var JamfProOverviewScreen = React.createClass({
    displayName: 'JamfProOverviewScreen',
    render: function () {

      var modals = _.map(config.options.modals, function (modalConfig, i) {
        var tagline = modalConfig.tagline && modalConfig.tagline.length ? <h4>{modalConfig.tagline}</h4> : null;
        var paragraphs = _.map(modalConfig.paragraphs, function (content) {
          return <p>{content}</p>
        });
        return (
          <script id={'modal-' + (i+1)} type="text/template">
            <h2>
              <img className="logo" src="./img/jamf-pro/logo-jamf-pro.svg" />
              {modalConfig.title}
            </h2>
            <div className="image">
              <img src={modalConfig.imgSrc} />
            </div>
            <div className="description">
              {tagline}
              {paragraphs}
            </div>
          </script>)
      });

      return (
        <div>
          <h1>{i18n.properties.jamf_pro_overview.title}</h1>
          <div className="row row-hero">
            <div className="col-md-6 col-md-offset-1">
              <a data-trigger-screenshot-modal="jamf-pro-screenshot-modal">
                <img src="./img/jamf-pro/screenshot-dashboard.png" />
              </a>
            </div>
            <div className="col-md-4">
              <img className="logo" src="./img/jamf-pro/logo-jamf-pro.svg" />
              <p>{i18n.properties.jamf_pro_overview.blurb}</p>
            </div>
          </div>
          <div className="row row-buttons">
            <button data-trigger-feature-modal="modal-1">{i18n.properties.jamf_pro_overview.deployment}</button>
            <button data-trigger-feature-modal="modal-2">{i18n.properties.jamf_pro_overview.apps_and_software}</button>
            <button data-trigger-feature-modal="modal-3">{i18n.properties.jamf_pro_overview.security}</button>
            <button data-trigger-feature-modal="modal-4">{i18n.properties.jamf_pro_overview.settings}</button>
            <button data-trigger-feature-modal="modal-5">{i18n.properties.jamf_pro_overview.inventory}</button>
            <button data-trigger-feature-modal="modal-6">{i18n.properties.jamf_pro_overview.self_service}</button>
          </div>
          <script id="jamf-pro-screenshot-modal" type="text/template">
            <img src="./img/jamf-pro/screenshot-dashboard.png" />
          </script>
          {modals}
        </div>
      );
    }
  });

  ReactDOM.render(
    <JamfProOverviewScreen />,
    section.get(0)
  );
};;
