// Get the pricing information for a NEW product (Subscription only, simpler quantity breakpoints)
// Currently only applies to apple + edu + subscription
// Planned to spread to other verticals, which is why that remains a param
JP.getNewProductPricingFromPriceList = function (productId, vertical, support) {
  var license = 'subscription';                                 // Only subscription is supported in new pricing model
  var productKey = 'new_apple_' + vertical;                     // Keys prepended by "new_apple_"
  var productData = _.find(JP.currentPriceList.products, {
    "product": productId,
    "license": license,
    "support": support
  });

  if (productData && productData[productKey]) {
    return productData[productKey];
  } else {
    console.error('Error: product pricing not found for new product: ' + productId + ', vertical: ' + vertical + ', license: ' + license + ', support: '
      + support + ' in pricing:', productKey);
    return null;
  }
};