JP.calculateTotals = function (productSelections, isJamfNow) {

  var totals = {};

  var license = productSelections['license'];
  var support = productSelections['support'];
  var quantityForDiscount = isJamfNow ? JP.getQuantity('jamf_now', productSelections) : JP.getQuantity('macos', productSelections) + JP.getQuantity('ios', productSelections);

  // Set up buckets
  totals['software_and_support_total'] = 0;
  totals['renewals'] = [];
  totals['software_and_support_total_discounted'] = 0;

  // Fill buckets with full retail and discounted costs for main products
  _.each(_.map(JP.PRODUCTS, 'id'), function (productId) {
    totals['software_and_support_total'] += JP.calculateProductCost(productId, productSelections, 0, null);
    totals['software_and_support_total_discounted'] += JP.calculateProductCost(productId, productSelections, quantityForDiscount, totals.renewals);
  });

  // Add in premium support and services
  var premiumSupportCost = JP.getPremiumSupportCost(productSelections, totals.renewals);
  totals['software_and_support_total'] += premiumSupportCost;
  totals['software_and_support_total_discounted'] += premiumSupportCost;
  var premiumServiceCost = JP.getPremiumServiceCost(productSelections, totals.renewals);
  totals['software_and_support_total'] += premiumServiceCost;
  totals['software_and_support_total_discounted'] += premiumServiceCost;

  // Round off
  totals['software_and_support_total'] = Math.ceil(totals['software_and_support_total']);
  totals['software_and_support_total_discounted'] = Math.ceil(totals['software_and_support_total_discounted']);

  // Total up the volume discount
  totals['discount_total'] = totals['software_and_support_total_discounted'] - totals['software_and_support_total'];

  // Total up the jumpstart costs
  totals['jumpstart_total'] = JP.getJumpStartCost(productSelections);

  // Total up the training pass costs
  totals['training_pass_total'] = JP.getTrainingPassCost(productSelections);

  // Grand total
  totals['overall_total'] =
    totals['software_and_support_total']
    + totals['discount_total']
    + totals['jumpstart_total']
    + totals['training_pass_total'];
  
  return totals;
};