JP.GetStartedForm = function () {

  return React.createClass({
    displayName: 'GetStartedForm',

    render: function () {

      console.log('this.props.viewConfig', this.props.viewConfig);
      console.log('this.props.stores', this.props.stores)

      var resellerCountry = null;
      if (this.props.resellerCountries.length === 1) {
        resellerCountry = (
          <div className="mktoFormRow">
            <label for="ResellerCountry">Country</label>
            <select id="ResellerCountry" name="ResellerCountry" className="form-control" tabindex="1">
              <option>Select country...</option>
              {_.map(this.props.resellerCountries, function (country) {
                return <option value={country}>{country}</option>
              })}
            </select>
          </div>
        )
      } else {
        resellerCountry = (
          <div className="mktoFormRow">
            <label for="ResellerCountry">Country</label>
            <select id="ResellerCountry" name="ResellerCountry" className="form-control" tabindex="1">
              <option>Select country...</option>
              {_.map(this.props.resellerCountries, function (country) {
                return <option value={country}>{country}</option>
              })}
            </select>
          </div>
        )
      }

      var resellerState = null;
      if (this.props.resellerStates.length) {
        resellerState = (
          <div className="mktoFormRow">
            <label for="ResellerState">State</label>
            <select id="ResellerState" name="ResellerState" className="form-control" tabindex="1">
              <option>Select store...</option>
              {_.map(this.props.resellerStates, function(state){
                return <option value={state}>{state}</option>
              })}
            </select>
          </div>
        )
      }

      var resellerStore = null;
      if (this.props.resellerStores.length) {
        resellerStore = (
          <div className="mktoFormRow">
            <label for="ResellerStore">Apple Location</label>
            <select id="ResellerStore" name="ResellerStore" className="form-control" tabindex="1">
              <option>Select store...</option>
              {_.map(this.props.resellerStores, function (store) {
                return <option value={store.name}>{store.name}</option>
              })}
            </select>
          </div>
        )
      }

      // // TODO: What is this vs the other store fields?!
      var resellerAppleStore = null;
      if (this.props.resellerAppleStore) {
        resellerAppleStore = (
          <div className="mktoFormRow">
            <label for="Reseller_Company__c" className="mktoLabel mktoHasWidth">Apple Location
            </label>
            <input id="Reseller_Company__c" name="Reseller_Company__c" maxlength="155" type="text"
                   className="form-control" tabindex="1"/>
          </div>
        )
      }

      var resellerEmail = null;
      if (this.props.resellerEmail) {
        resellerEmail = (
          <div className="mktoFormRow">
            <label for="Reseller_Contact_Email__c">Email Address
            </label>
            <input id="Reseller_Contact_Email__c" name="Reseller_Contact_Email__c" maxlength="255" type="email"
                   className="form-control"
                   tabindex="1"/>
          </div>
        )
      }

      var customerState = null;
      if (this.props.customerState) {
        customerState = (
          <div className="mktoFormRow">
            <label for="State">State</label>
            <select id="State" name="State" className="form-control"
                    tabindex="1">
              <option value="">Select...</option>
              {_.map(JP.marketoUtils.getUSStates(), function (state) {
                return <option value={state}>{state}</option>
              })}
            </select>
          </div>
        )
      }

      return (
        <form className="form-vertical lpeRegForm mktoForm">
          <div className="col-md-6 reseller">
            <h3>About you</h3>
            <div className="mktoFormRow row-half-conjoined-left">
              <label for="ResellerFirstName">First Name</label>
              <input id="ResellerFirstName" name="ResellerFirstName" maxlength="255" type="text"
                     className="form-control"
                     tabindex="1"/>
            </div>
            <div className="mktoFormRow row-half-conjoined-right">
              <label for="ResellerLasttName">Last Name</label>
              <input id="ResellerLastName" name="ResellerLastName" maxlength="255" type="text" className="form-control"
                     tabindex="1"/>
            </div>
            <div className="mktoFormRow row-hidden">
              <label for="Reseller_Contact_Name__c">
                Reseller Contact Name
              </label>
              <input id="Reseller_Contact_Name__c" name="Reseller_Contact_Name__c" maxlength="55" type="text"
                     className="form-control" tabindex="0"/>
            </div>
            {resellerCountry} {resellerState} {resellerStore} {resellerAppleStore} {resellerEmail}
            <div className="mktoFormRow">
              <label for="Internal_Sales_Notes__c">
                Initial Comments and Questions
              </label>
              <textarea id="Internal_Sales_Notes__c" name="Internal_Sales_Notes__c" rows="2"
                        className="form-control" maxlength="32000"
                        tabindex="1"></textarea>
            </div>
          </div>
          <div className="col-md-6 customer">
            <h3>About your customer</h3>
            <div className="mktoFormRow">
              <label for="FirstName">First Name</label>
              <input id="FirstName" name="FirstName" maxlength="255" type="text"
                     className="form-control"
                     tabindex="1"/>
            </div>
            <div className="mktoFormRow">
              <label for="LastName">Last Name</label>
              <input id="LastName" name="LastName" maxlength="255" type="text"
                     className="form-control"
                     tabindex="1"/>
            </div>
            <div className="mktoFormRow">
              <label for="Email">Email Address</label>
              <input id="Email" name="Email" maxlength="255" type="email"
                     className="form-control"
                     tabindex="1"/>
            </div>
            <div className="mktoFormRow">
              <label for="Company">Company Name</label>
              <input id="Company" name="Company" maxlength="255" type="text"
                     className="form-control"
                     tabindex="1"/>
            </div>
            <div className="mktoFormRow">
              <label for="numberofAppleDevices">Device Count
              </label>
              <input id="numberofAppleDevices" name="numberofAppleDevices" maxlength="12" type="number"
                     className="form-control" min="" max=""
                     step=""
                     tabindex="1"/>
            </div>
            {customerState}
            <div className="mktoFormRow">
              <label for="Country">Country</label>
              <select id="Country" name="Country" className="form-control"
                      tabindex="1">
                <option selected="selected">The Netherlands</option>
              </select>
            </div>
            <div className="mktoFormRow">
              <label for="Industry">Industry</label>
              <select id="Industry" name="Industry" className="form-control"
                      tabindex="1">
                <option value="">Select...</option>
                {_.map(JP.marketoUtils.getIndustries(), function (industry) {
                  return <option value={industry}>{industry}</option>
                })}
              </select>
            </div>
            <div className="mktoFormRow">
              <input type="hidden" name="Goals__c"
                     value="<br>Products and Services:<br><br> * MACOS: undefined devices<br> * IOS: undefined devices<br> * SUPPORT: 3 year<br> * LICENSE: subscription<br> * JUMPSTART: 3 Day Combined<br><br>First 3 years <br><br><brYearly Renewal:<br>"/>
            </div>
            <div className="mktoFormRow">
              <input type="hidden" name="Solution_Requested__c"
                     value=""/>
            </div>
            <div className="mktoFormRow">
              <input type="hidden" name="Apple_Source__c"
                     value=""/>
            </div>
          </div>
        </form>
      )
    }
  });
};
;
