
JP.EnterpriseTechnologyScreen = function (section) {
  var config = JP.getViewConfig('enterprise-technology');
  var SixTileGrid = JP.SixTileGrid();
  var EnterpriseTechnologyTiles = React.createClass({
    displayName: 'EnterpriseTechnologyTiles',
    render: function () {
      return <SixTileGrid tiles={config.options.tiles}></SixTileGrid>
    }
  });

  ReactDOM.render(
    <EnterpriseTechnologyTiles />,
    section.get(0)
  );
};;
