// Namespace for the JAMF MDM Pricing Web app
var JP = {};

// Initialize globals
JP.rootElem = null;
JP.useLocalCurrency = false;

// Initialize fastclick
$(function () {
  Origami.fastclick(document.body);
});

// If switching to new edition (new distributor, vertical, or lang), clear localStorage to prevent weird state issues
$(function () {
  if (localStorage.getItem('JP_DISTRIBUTOR') !== JP.config.distributor
    || localStorage.getItem('JP_VERTICAL') !== JP.config.vertical
    || localStorage.getItem('JP_LANG') !== i18n.properties.lang) {
    localStorage.clear();
    localStorage.setItem('JP_DISTRIBUTOR', JP.config.distributor);
    localStorage.setItem('JP_VERTICAL', JP.config.vertical);
    localStorage.setItem('JP_LANG', i18n.properties.lang);
  }
});

// Fix for iPad Safari issue
// When the tab bar opens to accommodate a new tab, upon returning to our app, the touch input will be offset (sometimes)
// by the height of the tab-bar.  Scrolling manually (or programmatically) seems to fix this.
$(function () {
  $(window).on('resize', function () {
    window.scrollTo(0, 1);
  });
});