JP.getJumpStartState = function (productSelections) {

  var hasMacOS = productSelections.macos.active;
  var hasIOS = productSelections.ios.active;
  var licenseIsSubscription = productSelections.license === 'subscription';

  var jumpStartState = JP.JUMP_START_STATES['EMPTY'];

  // macOS only
  if (hasMacOS && !hasIOS) {
    jumpStartState = JP.JUMP_START_STATES['2_day_macos'];
  }

  // iOS only - subscription
  else if (!hasMacOS && hasIOS && licenseIsSubscription) {
    jumpStartState = JP.JUMP_START_STATES['half_day_remote'];
  }

  // iOS only - perpetual
  else if (!hasMacOS && hasIOS && !licenseIsSubscription) {
    jumpStartState = JP.JUMP_START_STATES['half_day_remote_2x'];
  }

  // macOS & iOS - subscription
  else if (hasMacOS && hasIOS && licenseIsSubscription) {
    jumpStartState = JP.JUMP_START_STATES['3_day_combined'];
  }

  // macOS & iOS - perpetual
  else if (hasMacOS && hasIOS && !licenseIsSubscription) {
    jumpStartState = JP.JUMP_START_STATES['3_day_combined'];
  }

  return jumpStartState;
};
