JP.getPremiumSupportCost = function (productSelections, renewals) {
  var annualCost = 0;
  var pricing;
  var selectedSupportProductId;

  _.forEach(['premium_support', 'premium_support_plus', 'premium_support_enterprise'], function (supportProductId) {
    if (productSelections[supportProductId]) {
      selectedSupportProductId = supportProductId;
      return false;
    }
  });

  if (selectedSupportProductId) {
    pricing = _.find(JP.currentPriceList.products, {product: selectedSupportProductId});
    if (pricing && pricing.price) {
      annualCost = parseFloat(pricing.price);
    } else {
      console.error('Error: pricing for premium support not found.');
    }
  }

  if (annualCost) {
    renewals.push({
      id: selectedSupportProductId,
      displayName: 'Premium Support',
      unitCost: '--',
      totalCost: annualCost
    })
  }

  return annualCost;
};