
JP.SixTileGrid = function () {
  return React.createClass({
    displayName: 'SixTileGrid',
    render: function () {
      var tiles = _.map(this.props.tiles, function (tileConfig) {
        var points = _.map(tileConfig.points, function (point) {
          return <li>{point}</li>
        });
        // External links open in new windows
        var target = tileConfig.href.indexOf('//') === -1 ? "_self" : "_blank";
        return <li>
          <a href={tileConfig.href} title={tileConfig.title} target={target}>
            <div className="image">
              <img src={tileConfig.imgSrc} style={tileConfig.imgStyleOverrides} />
            </div>
            <h4>{tileConfig.title}</h4>
            <ul>{points}</ul>
          </a>
        </li>
      });

      var noTilesHavePoints = !(_.find(this.props.tiles, function(tileConfig){
        return tileConfig.points.length > 0
      }));

      var boxGridClassName = 'box-grid';
      if (tiles.length === 4) {
        boxGridClassName += ' has-four-tiles';
      }
      if (noTilesHavePoints) {
        boxGridClassName += ' no-tiles-have-points'
      }

      return (
        <div className="row">
          <div className="col-md-12">
            <ul className={boxGridClassName}>
              {tiles}
            </ul>
          </div>
        </div>
      );
    }
  });
};;
