JP.LicenseToggleLink = function () {
  return React.createClass({
    displayName: 'LicenseToggleLink',
    render: function () {
      var licenseName = this.props.license === 'subscription' ? i18n.properties.pricing.license['perpetual'] : i18n.properties.pricing.license.subscription;
      return (
        <div className="col-sm-6 col-sm-pull-6 col-md-12 col-md-pull-0">
          <a className="mode-toggle-link" onClick={this.props.licenseToggled}>{'Looking for ' + licenseName + ' pricing?'}</a>
        </div>
      )
    }
  });
};;
