JP.MainProductToggle = function(){
  var QuantityStepper = JP.QuantityStepper();

  return  React.createClass({
    displayName: 'MainProductToggle',
    outerAreaClicked: function () {
      this.props.toggleProduct();
    },
    render: function () {
      return (
        <div className={'segment segment-top-bottom-combined' + (this.props.active ? ' active' : '')}
             onClick={this.outerAreaClicked}>
          <div className="segment-top">{this.props.productName}</div>
          <div className="segment-bottom">
            <QuantityStepper quantity={this.props.quantity} step={10}
                             quantityChanged={this.props.quantityChanged}
                             toggleProduct={this.props.toggleProduct}
                             enforceQuantityBounds={this.props.enforceQuantityBounds} />
          </div>
        </div>
      )
    }
  });
};;
