JP.prettifyPricing = function (pricingData) {

  var
    products = '<br>Products and Services:<br>',
    otherItems = '',
    totals = '<br><br>' + (pricingData['support'] === '1_year' ? 'First year' : 'First 3 years') + ' <br>',
    renewalCosts = '<br><brYearly Renewal:<br>',
    label = function (text) {
      return '<br> * ' + text.replace(/_/g, ' ').toUpperCase() + ': ';
    };

  _.each(pricingData, function (value, key) {

    // macOS and IOS
    if (key === 'macos' || key === 'ios') {
      if (value['active']) {
        products += label(key) + value['value'] + ' devices';
      }
    }

    // License, Support
    else if (key === 'license' || key === 'support') {
      otherItems += label(key) + value.replace(/_/g, ' ');
    }

    // JumpStart
    else if (key === 'jumpstart') {
      otherItems += label(key) + value.state.name;
    }

    // Totals
    else if (key.indexOf('total') !== -1) {
      totals += label(key) + JP.formatAsActiveCurrency(value);
    }

    // Renewal Costs
    else if (key === 'renewal_costs') {
      var totalRenewals = 0;
      _.each(value, function (renewalVal, renewalKey) {
        renewalCosts += label(renewalKey) + JP.formatAsActiveCurrency(renewalVal[0]) +
          (pricingData['support'] === '1_year' ? ' (per seat), ' : ' (total), ') +
          JP.formatAsActiveCurrency(renewalVal[1]) + ' (renewal)';
        totalRenewals += renewalVal[1];
      });
      renewalCosts += label('Total') + ' ' + JP.formatAsActiveCurrency(totalRenewals);
    }
  });
  return products + otherItems + totals + renewalCosts;
};