JP.calculateProductCost = function (productId, productSelections, quantityForDiscount, renewalCosts) {

  var quantity = JP.getQuantity(productId, productSelections);
  var license = productSelections['license'];
  var support = productSelections['support'];
  var vertical = JP.config.vertical || 'com';

  var isJamfNowSelection = productId === JP.PRODUCTS.JAMF_NOW.id && quantity;
  var isJamfProSelection = !isJamfNowSelection && productId && quantity && vertical && license && support;

  if (!isJamfNowSelection && !isJamfProSelection) {
    return 0;
  }

  if (isJamfNowSelection) {
    return getJamfNowProductCost(productId, quantity, vertical, license, support, quantityForDiscount, renewalCosts);
  }

  // New pricing scheme for main products
  if (JP.productUsesNewPricingModel(productId, productSelections)) {
    return getNewJamfProProductCost(productId, quantity, vertical, support, productSelections.jumpstart.active, quantityForDiscount, renewalCosts);
  }

  return getJamfProProductCost(productId, quantity, vertical, license, support, quantityForDiscount, renewalCosts);

  // Amount of discount caused by "3 first devices free"
  function getJamfNowDiscountAmount(unitCost, quantity) {
    return unitCost * Math.min(quantity, 3)
  }

  function getNewJamfProProductCost(productId, quantity, vertical, support, jumpStartSelected, quantityForDiscount, renewalCosts) {

    // Pricing format is an array of 2 numbers:
    //   [ seat_cost, seat_cost_10k+ ]

    var pricing = JP.getNewProductPricingFromPriceList(productId, vertical, support);
    var firstYearCost = 0;
    var annualRenewalCost = 0;
    var perSeatRenewalCost = 0;

    // < 10,000 quantity pricing
    if (quantityForDiscount < 10000) {
      firstYearCost += Math.max((quantity * pricing.prices[1]), 0);
      perSeatRenewalCost = pricing.prices[1];
      annualRenewalCost = quantity * perSeatRenewalCost;
    }

    // >= 10,000 quantity
    if (quantityForDiscount >= 10000) {
      firstYearCost += Math.max((quantity * pricing.prices[2]), 0);
      perSeatRenewalCost = pricing.prices[2];
      annualRenewalCost = quantity * perSeatRenewalCost;
    }

    if (renewalCosts) {
      renewalCosts.push({
        id: productId,
        displayName: JP.PRODUCTS[productId.toUpperCase()].displayName,
        unitCost: perSeatRenewalCost,
        totalCost: annualRenewalCost
      });
    }

    return support === '3_year' ? (firstYearCost + (2 * annualRenewalCost)) : firstYearCost;
  }

  function getJamfNowProductCost(productId, quantity, vertical, license, support, quantityForDiscount, renewalCosts) {
    var productPricing = JP.getProductPricingFromPriceList(productId, support, vertical, license);
    var unitCost = JP.getUnitCost(productId, quantityForDiscount, vertical, license, support, productPricing, 0);
    var monthlyCost = quantity * unitCost;

    if (quantityForDiscount) {
      monthlyCost -= getJamfNowDiscountAmount(unitCost, quantity);
    }

    if (renewalCosts) {
      renewalCosts.push({
        id: productId,
        displayName: JP.PRODUCTS[productId.toUpperCase()].displayName,
        unitCost: unitCost,
        totalCost: (unitCost * quantity) - getJamfNowDiscountAmount(unitCost, quantity)
      });
    }

    return support === '2_year' ? monthlyCost * 24 : monthlyCost * 12;
  }

  function getJamfProProductCost(productId, quantity, vertical, license, support, quantityForDiscount, renewalCosts) {
    var discountIndex = JP.getDiscountIndexForQuantity(quantityForDiscount);
    var productPricing = JP.getProductPricingFromPriceList(productId, support, vertical, license);
    var unitCost = JP.getUnitCost(productId, quantityForDiscount, vertical, license, support, productPricing, discountIndex);

    // Renewal costs work oddly, ATM.  The renewalCosts object that's passed in gets modified, but not returned.
    // TODO: can we separate the renewalCosts workflow somehow?
    // --
    // Add annual renewal costs, representing the costs of year 2 (or year 4 for 3-year term)
    if (renewalCosts) {
      renewalCosts.push({
        id: productId,
        displayName: JP.PRODUCTS[productId.toUpperCase()].displayName,
        // SCCM does not have a per-seat cost
        unitCost: productId === JP.PRODUCTS.SCCM_PLUGIN.id ? '--' : productPricing.renewal[discountIndex],
        totalCost: productPricing.renewal[discountIndex] * quantity
      });
    }

    return quantity * unitCost;
  }
};