JP.initGetStartedScreen = function (section) {

  var viewConfig = JP.getViewConfig('get-started');
  var marketoFormReadyRun = false;
  var STORES = [];  // To be populated from API later

  function getTextField(fieldName) {
    return $('<input id="' + fieldName + '" name="' + fieldName + '" maxlength="255" type="text" class="form-control" tabindex="1">');
  }

  function getSelect(fieldName) {
    return $('<select id="' + fieldName + '" name="' + fieldName + '" class="form-control" tabindex="1"></select>')
  }

  function getOption(optionValue) {
    return $('<option value="' + optionValue + '">' + optionValue + '</option>');
  }

  function getRow(fieldName, labelText) {
    return $('<div class="mktoFormRow"><label for="' + fieldName + '">' + labelText + '</label></div>');
  }

  function createSectionsAndHeaders() {
    section.find('.mktoFormRow').slice(0, 4).wrapAll('<div class="col-md-6 reseller"></div>').first().before('<h3>' + i18n.properties.get_started.you.title + '</h3>');
    section.find('.mktoFormRow').slice(4, 20).wrapAll('<div class="col-md-6 customer"></div>').first().before('<h3>' + i18n.properties.get_started.customer.title + '</h3>');
  }

  function splitResellerIntoFirstNameLastName() {
    // Front the reseller name field with separate FirstName and LastName fields that combine to form one name
    var
      resellerNameField = section.find('input[name=Reseller_Contact_Name__c]'),
      resellerNameRow = resellerNameField.closest('.mktoFormRow'),
      firstNameField = getTextField('ResellerFirstName'),
      firstNameRow = getRow('ResellerFirstName', i18n.properties.get_started.you.first_name).append(firstNameField),
      lastNameField = getTextField('ResellerLastName'),
      lastNameRow = getRow('ResellerLasttName', i18n.properties.get_started.you.last_name).append(lastNameField);

    // Combine firstName and lastName
    firstNameRow.add(lastNameRow).find('input').on('change', function () {
      resellerNameField.val(firstNameField.val() + ' ' + lastNameField.val());
    });

    // Squish this field in such a way hide it while still allowing it's validation message to display
    resellerNameRow.css({
      marginBottom: 0,
      marginTop: '-10px'
    });
    resellerNameRow.find('label').hide();
    resellerNameRow.find('input').css({
      zIndex: -1,
      position: 'absolute',
      opacity: 0
    }).attr('tabindex', 0);
    resellerNameField.on('focus', function () {
      firstNameField.trigger('focus');
    });
    resellerNameRow.before(firstNameRow);
    resellerNameRow.before(lastNameRow);
  }

  function prepopulateHiddenPricingAndNumberOfDevices() {
    var storedPricing = JP.savedProductSelections.get(false);
    if (storedPricing) {
      section.find('[name=Goals__c]').val(JP.prettifyPricing(storedPricing));

      var totalDevices = 0;
      _.each(['macos', 'ios'], function (productId) {
        if (storedPricing[productId] && storedPricing[productId]['active']) {
          totalDevices += parseInt(storedPricing[productId]['value']);
        }
      });
      if (totalDevices > 0) {
        section.find('#numberofAppleDevices').val(totalDevices);
      }
    }
  }

  function addLocalizedCountryStateStoreDropdowns() {
    var
      marketoAppleStoreField = section.find('#Reseller_Company__c'),
      marketoTeamEmailField = section.find('#Reseller_Contact_Email__c'),
      appleSourceField = section.find('[name=Apple_Source__c]'),
      countryDropdown = getSelect('ResellerCountry'),
      countryRow = getRow('ResellerCountry', i18n.properties.get_started.you.country).append(countryDropdown),
      stateDropdown = getSelect('ResellerState'),
      stateRow = getRow('ResellerState', i18n.properties.get_started.you.state).append(stateDropdown),
      storeDropdown = getSelect('ResellerStore'),
      storeRow = getRow('ResellerStore', i18n.properties.get_started.you.apple_store).append(storeDropdown);

    function setCountries() {
      var countries = _.sortBy(_.uniq(_.map(STORES, 'country')));
      var countryOptions = _.map(countries, function (country) {
        return getOption(country);
      });
      if (countries.length <= 1) {
        countryRow.hide();
      }
      countryDropdown.html(countryOptions);
      selectSavedOption(countryDropdown, 'country');
    }

    function setStates() {
      var selectedCountry = countryDropdown.val();
      var states = _.sortBy(_.uniq(_.map(_.filter(STORES, {'country': selectedCountry}), 'state')));
      var stateOptions = _.map(states, getOption);

      // if US edition, add an Online option
      if (i18n.properties.lang === 'en-US') stateOptions.unshift(getOption('Online'));

      if (states.length <= 1) {
        stateRow.hide();
      }
      stateDropdown.html(stateOptions);
      selectSavedOption(stateDropdown, 'state');
    }

    function setStores() {
      var selectedCountry = countryDropdown.val();
      var stateVal = stateDropdown.val();

      // Filter by Country
      var relevantStores = _.filter(STORES, {country: selectedCountry});

      // For US and Canada, also filter by State (and handle Online)
      if (selectedCountry === 'US' || selectedCountry === 'Canada') {
        relevantStores = _.filter(relevantStores, function (store) {

          // If Online, look for stores with Online in the name
          if (stateVal === 'Online') {
            return store.name.indexOf('Online') !== -1;
          }

          // Otherwise include stores from the selected state
          return stateVal && stateVal !== 'null' && store.state === stateVal;
        });
      }

      var storeNames = _.sortBy(_.map(relevantStores, 'name'));
      var storeOptions = _.map(storeNames, getOption);

      // Add a blank option so users actively select the correct store
      storeOptions.unshift($('<option>Select store...</option>'));
      storeDropdown.html(storeOptions);

      selectSavedOption(storeDropdown, 'store');
      setMarketoFieldVals();
    }

    function setMarketoFieldVals() {
      var storeName = storeDropdown.val();
      var store = _.find(STORES, {'name': storeName});

      marketoAppleStoreField.val(storeName);
      marketoTeamEmailField.val(store ? store['email'] : '');
      appleSourceField.val(store ? store['salesforceid'] : '');

      // Only show the email field if the state is "Online" and a store is selected
      var storeIsSet = typeof store === 'object';
      var stateIsOnline = stateDropdown.val() === 'Online';
      var showEmail = storeIsSet && stateIsOnline;
      var emailRow = marketoTeamEmailField.closest('.mktoFormRow');
      if (showEmail) {
        emailRow.slideDown(300);
      } else {
        emailRow.slideUp(300);
      }
    }

    setCountries();
    setStates();
    setStores();
    countryDropdown.on('change', setStates);
    stateDropdown.on('change', function () {
      setStores();
      saveResellerInformation();
    });
    storeDropdown.on('change', function () {
      saveResellerInformation();
      setMarketoFieldVals();
    });

    marketoAppleStoreField.closest('.mktoFormRow').hide().before([countryRow, stateRow, storeRow]);
  }

  function saveResellerInformation() {
    // We save all of this info, but it is not always used.
    // We only reinstate name and email when Online has been selected
    var info = {
      country: $('#ResellerCountry').val(),
      state: $('#ResellerState').val(),
      store: $('#ResellerStore').val(),
      email: $('#Reseller_Contact_Email__c').val(),
      firstName: $('#ResellerFirstName').val(),
      lastName: $('#ResellerLastName').val()
    };
    localStorage.setItem('JP_ResellerInformation', JSON.stringify(info));
  }

  function getSavedResellerInfo() {
    return JSON.parse(localStorage.getItem('JP_ResellerInformation'));
  }

  function selectSavedOption(dropdown, dataKey) {
    var savedInfo = getSavedResellerInfo();
    if (savedInfo && savedInfo[dataKey]) {
      dropdown.find('option[value="' + savedInfo[dataKey] + '"]').attr('selected', 'selected');
    }
  }

  function processCustomerStateAndCountryDropdowns() {
    if (i18n.properties.lang !== 'en-US') {
      var marketoStateDropdown = section.find('#State');
      var marketoStateRow = marketoStateDropdown.closest('.mktoFormRow');
      var marketoCountryDropdown = section.find('#Country');
      var marketoCountryRow = marketoCountryDropdown.closest('.mktoFormRow');

      marketoCountryDropdown.html('<option selected="selected">' + i18n.properties.countryNameInEnglish + '</option>');
      marketoCountryRow.hide();
      marketoStateRow.hide();
    }
  }

  function prepareNameAndEmailFields() {
    // If Online location selected, populate fields from saved state
    var savedInfo = getSavedResellerInfo();
    if (savedInfo && savedInfo.state && savedInfo.state === 'Online' &&
      savedInfo.store && savedInfo.store.indexOf('Online') !== -1) {
      $('#ResellerFirstName').val(savedInfo.firstName);
      $('#ResellerLastName').val(savedInfo.lastName);
      $('#Reseller_Contact_Email__c').val(savedInfo.email);
    }

    // On change, save state
    $('#ResellerFirstName, #ResellerLastName, #Reseller_Contact_Email__c').on('change', function () {
      saveResellerInformation();
    });
  }

  function callMarketoNewForm(formDescriptor) {
    MktoForms2.newForm(formDescriptor, function (form) {
      var lpFields = {
        "lpId": 1605,
        "subId": 16,
        "munchkinId": "704-VYP-527"
      };
      var pageFields = MktoForms2.getPageFields();
      form.addHiddenFields(lpFields);
      form.addHiddenFields(pageFields);
      if (!form.EnableDeferredMode) {
        form.render();
      }
      //Validate form
      form.onSubmit(function () {
        if (form.validate()) {
          form.submitable(true);
        }
        else {
          form.submitable(false);
        }
      });

      form.onSuccess(function (values, followUpUrl) {
        JP.modal.display('<p>' + i18n.properties.get_started.success + '</p>', 'success', 5000);
        JP.savedProductSelections.clear(false);
        section.find('button[type=submit]').text(i18n.properties.get_started.customer.contact_us).removeAttr('disabled');

        // Clear frequently changed fields
        section.find('#ResellerFirstName, #ResellerLastName, #Internal_Sales_Notes__c, #FirstName, #LastName, #Email, ' +
          '#Company, #numberofAppleDevices, #State, #Country, #Industry').filter(':visible').val('');

        return false;
      });
    });
  }

  // Get store list, then build the form
  JP.getStores().then(function (stores) {
    STORES = stores;

    JP.getFormDescriptorForGetStartedForm(viewConfig.options.marketoFormId)
      .then(function (customFormDescriptor) {
        
        // Set the ID for the form itself
        section.find('form').attr('id', "mktoForm_" + viewConfig.options.marketoFormId);

        // Trigger marketo's form builder
        callMarketoNewForm(customFormDescriptor);

        // Do around of customization
        MktoForms2.whenReady(function () {
          var form;
          if (!marketoFormReadyRun) {
            marketoFormReadyRun = true;
            form = section.find('form');
            JP.marketoUtils.cleanUpMarkup(form);
            createSectionsAndHeaders();
            addLocalizedCountryStateStoreDropdowns();
            processCustomerStateAndCountryDropdowns();
            splitResellerIntoFirstNameLastName();
            prepopulateHiddenPricingAndNumberOfDevices();
            prepareNameAndEmailFields();

            // Show the form, after a pause for show/hide animations to complete
            setTimeout(function () {
              form.attr('style', '').show();
            }, 320);

            // Auto-focus in iOS 8 is problematic: http://stackoverflow.com/questions/26146252/
            if (!(/(iPad|iPhone|iPod)/g.test(navigator.userAgent))) {
              getTextField('ResellerFirstName').trigger('focus');
            }
          }
        });
      }, function(error){
        console.error(error);
      });
  });
};
