// New pricing model applies to all EDU pricing (as of 10/2017)
JP.editionSupportsNewPricingModel = function(){
  return JP.config.vertical === 'edu';
};

// New pricing model only applies to macOS and iOS products, and only when active
JP.productUsesNewPricingModel = function(productId, productSelections){
  return (productId === 'macos' || productId === 'ios') && productSelections.newPricingModelActive;
};