JP.getExchangeRate = function () {

  var LS_KEY = 'JP_ExchangeRates';

  function setLocalStorage(rates) {
    localStorage.setItem(LS_KEY, JSON.stringify({
      'rates': rates,
      'timestamp': new Date().getTime()
    }));
  }

  function getFromLocalStorage() {
    return JSON.parse(localStorage.getItem(LS_KEY));
  }

  // Rates considered fresh for 12 hours
  function areFresh(rates) {
    var
      now = new Date(),
      nowInMillis = now.getTime(),
      timestampInMillis = parseInt(rates['timestamp']),
      twelveHoursInMillis = 43200000;
    return timestampInMillis > nowInMillis - twelveHoursInMillis;
  }

  var savedRates = getFromLocalStorage();

  if (savedRates && areFresh(savedRates)) {
    return parseFloat(savedRates.rates[i18n.properties.currency]);
  } else {
    console.info('Exchange rate missing or out of date.  Refreshing now.');
    $.getJSON("https://openexchangerates.org/api/latest.json?app_id=9bb4a643fc9b4a84bdadd9486437e889")
        .done(function (data) {
          setLocalStorage(data.rates);
        }).fail(function(error){
          console.error('Error fetching rates: ', error);
        });
    return false;
  }
};