JP.getJumpStartCost = function (productSelections) {
  var cost = 0;
  var jumpStartValue, jumpStartPricing;

  if (productSelections.jumpstart && productSelections.jumpstart.active) {

    jumpStartValue = productSelections.jumpstart.state.value;
    jumpStartPricing = _.find(JP.currentPriceList.products, {product: 'jumpstart', type: jumpStartValue});

    if (jumpStartPricing && jumpStartPricing.price) {
      cost = parseFloat(jumpStartPricing.price);
    } else {
      console.error('Error: JumpStart pricing for ' + jumpStartValue + ' not found.');
    }
  }

  return cost;
};