JP.configureNav = function () {

  var enabledViews =  _.filter(JP.config.views, 'enabled');

  if (enabledViews.length === 0){
    console.error("Error: no views were enabled in the edition config.");
  }

  // Nav is hidden by default, only show if more than one nav item configured
  if (enabledViews.length > 1){
    $('body').removeClass('hide-nav');
    // Go through all nav items, enabling those that are enabled in the config
    $('nav a[href]').each(function(i, elem){
      var $link = $(elem);
      var linkedViewName = $link.attr('href').replace('#', '');
      var viewConfig = JP.getViewConfig(linkedViewName);
      if (viewConfig.enabled){
        $link.closest('li').addClass('enabled');

        if (viewConfig.options.externalPageOverride){
          $link
            .attr('href', viewConfig.options.externalPageOverride.url)
            .attr('title', viewConfig.options.externalPageOverride.title)
            .attr('target', '_blank');
        }
      }
    });
  }

  // Set default view
  var defaultView = _.find(JP.config.views, 'default');
  JP.DEFAULT_VIEW = defaultView ? defaultView.name : 'overview';
};