JP.getTrainingPassCost = function (productSelections) {
  var cost = 0;
  _.each(['training_pass_individual', 'training_pass_institutional'], function(productName){
    if (productSelections[productName]){
      var pricing = _.find(JP.currentPriceList.products, {product: productName});
      if (pricing && pricing.price){
        cost += parseFloat(pricing.price);
      } else {
        console.error('Error: Pricing for ' + productName + ' not found.');
      }
    }
  });
  return cost;
};