JP.getDiscountIndexForQuantity = function(quantityForDiscount) {
  if (!JP.currentPriceList.quantityDiscountBreakpoints){
    console.error('Error in getDiscountIndexForQuantity. Relevant quantityDiscountBreakpoints not defined (yet).');
  } else {
    var discountIndex = 0;
     // For determining discount rate, we need to consider total quantity of iOS AND macOS products, not individual product quantities
     if (quantityForDiscount) {
       discountIndex = _.findIndex(JP.currentPriceList.quantityDiscountBreakpoints, function (discountBreakpoint) {
         return discountBreakpoint > quantityForDiscount;
       })
     }
     return discountIndex;
  }
};