// Under-the-hood utility function -- Look for missing properties in translations
JP.listMissingTranslations = function(){
  var out = i18n.properties.lang + ' is missing:\n';
  for (prop in i18n.properties) {
    for (prop2 in i18n.properties[prop]) {
      if (typeof i18n.properties[prop][prop2] === 'string' && i18n.properties[prop][prop2].indexOf('NEED') != -1) {
        out += prop + '.' + prop2 + '\n';
      } else {
        for (prop3 in i18n.properties[prop][prop2]) {
          if (i18n.properties[prop][prop2][prop3].indexOf('NEED') != -1) {
            out += prop + '.' + prop2 + '.' + prop3 + '\n';
          }
        }
      }
    }
  }
  console.log(out);
};