JP.PlanSummaryRenewalTable = function(){
  return React.createClass({
    displayName: 'PlanSummaryRenewalTable',

    // Note: this.props.renewals is an array of objects, like so:
    // [
    //   {
    //     id: 'ios',
    //     displayName: 'iOS',
    //     unitCost: 25,
    //     totalCost: 3500
    //   },
    //   ...
    // ]
    // The first number is the per-seat renewal cost.
    // The second number is the total renewal cost.

    render: function () {

      var rows = _.map(this.props.renewals, function (renewal) {

        // Override Jamf Now displayName if config'd
        var displayName = renewal.displayName;
        var viewConfig = JP.getViewConfig('jamf-now-plan');
        if (viewConfig.options.overrideJamfNowDisplayNameInSummary.enabled && displayName === 'Jamf Now') {
          displayName = viewConfig.options.overrideJamfNowDisplayNameInSummary.displayName;
        }

        return (
          <tr>
            <td>{displayName}</td>
            <td>{JP.formatAsActiveCurrency(renewal.unitCost)}</td>
            <td>{JP.formatAsActiveCurrency(renewal.totalCost)}</td>
          </tr>
        );
      });

      var total = _.reduce(this.props.renewals, function (sum, renewal) {
        return sum + renewal.totalCost;
      }, 0);
      total = JP.formatAsActiveCurrency(total);

      var title;
      if (this.props.subtitle) {
        title = <h5 className="with-subtitle">{this.props.title} <small>{this.props.subtitle}</small> </h5>;
      } else {
        title = <h5>{this.props.title}</h5>;
      }

      return (
        <div className="col-sm-6 col-sm-pull-6 col-md-12 col-md-pull-0 renewal-cost">
          <h5>{title}</h5>
          <table className="table table-condensed">
            <thead>
              <tr>
                <th></th>
                <th>{this.props.columnNames[0]}</th>
                <th>{this.props.columnNames[1]}</th>
              </tr>
            </thead>
            <tbody>
              {rows}
              <tr>
                <td></td>
                <td></td>
                <td>{total}</td>
              </tr>
            </tbody>
          </table>
        </div>
      )
    }
  });
};;
