JP.quoteDetails = {
  display: function (isJamfNow) {
    var content = $(JP.quoteDetails.getHTML(isJamfNow));

    // To allow copy/paste, don't allow inner content clicks to close modal
    content.on('click', function (event) {
      event.stopPropagation();
    });

    JP.modal.display(content, 'modal-quote', null);
  },

  getFootnoteContent: function (productSelections) {
    var content = '';

    // COM + APPLE
    if (JP.config.vertical === 'com' && JP.config.distributor === 'apple') {
      content += '<b>' + i18n.properties.pricing.quote.footnote_lead + '</b> ' + i18n.properties.pricing.quote.footnote_content;
    }

    // PARTNERS
    else if (JP.config.distributor === 'jamf') {
      content += '<b>' + i18n.properties.pricing.quote.footnote_lead + '</b> ' + i18n.properties.pricing.quote.footnote_content_partner;
    }

    // Add global notes
    // TODO: localize
    content += content.length ? '<br><br>' : '';
    content += '<br><br><b>Jamf Pro requires a ' + JP.getJamfProMinQuantity() + ' device minimum.</b>';

    return content;
  },

  getHTML: function (isJamfNow) {
    var productSelections = JP.savedProductSelections.get(isJamfNow);
    var html = '<p>Quoting Instructions</p>';
    var headerPrefix = '';
    var mainBoxHeader = '';
    var mainBoxRows = '';
    var secondaryBoxHeader = '';
    var secondaryBoxRows = '';
    var footnoteContent = this.getFootnoteContent(productSelections);
    var footnote = '<p class="footnote">' + footnoteContent + '</p>';
    var isNewPricing = productSelections.newPricingModelActive;
    var premiumSupportHTML = JP.quoteDetails.getRowForPremiumSupport(productSelections);
    var premiumServiceHTML = JP.quoteDetails.getRowForPremiumService(productSelections);
    var jumpStartHTML = JP.quoteDetails.getRowForJumpStart(productSelections);
    var trainingPassHTML = JP.quoteDetails.getRowsForTrainingPass(productSelections);

    headerPrefix += '<h3>';
    headerPrefix += JP.config.vertical === 'com' ? i18n.properties.pricing.quote.commercial : i18n.properties.pricing.quote.educational;
    headerPrefix += isJamfNow ? ' – ' : ' – ' + i18n.properties.pricing.quote.jamf_pro + ' – ';

    mainBoxHeader += headerPrefix;
    mainBoxHeader += productSelections.license === 'subscription' ? i18n.properties.pricing.quote.subscription_license : i18n.properties.pricing.quote.perpetual_license;
    mainBoxHeader += isNewPricing ? ' – ' + i18n.properties.pricing.quote.first_year : '';
    mainBoxHeader += '</h3>';

    secondaryBoxHeader += headerPrefix;
    secondaryBoxHeader += productSelections.license === 'subscription' ? i18n.properties.pricing.quote.subscription_license : i18n.properties.pricing.quote.perpetual_license;
    secondaryBoxHeader += ' – ' + i18n.properties.pricing.quote.second_and_third_years;
    secondaryBoxHeader += '</h3>';

    // Main products (main SKUs and renewal/support SKUs when present)
    _.each(_.map(JP.PRODUCTS, 'id'), function (productId) {
      if (JP.getQuantity(productId, productSelections) > 0) {

        // Check if we should use the "new" product scheme
        var rows = JP.productUsesNewPricingModel(productId, productSelections) ?
          JP.quoteDetails.getRowsForNewProduct(productId, productSelections) :
          JP.quoteDetails.getRowsForLegacyProduct(productId, productSelections);

        rows.mainBoxRows.forEach(function (row) {
          mainBoxRows += row;
        });
        rows.secondaryBoxRows.forEach(function (row) {
          secondaryBoxRows += row;
        });
      }
    });

    mainBoxRows += jumpStartHTML + premiumSupportHTML + premiumServiceHTML + trainingPassHTML;

    html += '<div class="panel">';
    html += '<h2>' + i18n.properties.pricing.quote.license_type + '</h2>';
    html += mainBoxHeader;
    html += '<table>' + mainBoxRows + '</table>';

    // // Renewals/support, when appropriate
    if (secondaryBoxRows.length) {
      html += secondaryBoxHeader;
      html += '<table>'
        + secondaryBoxRows
        + '</table>';
    }

    html += footnote;
    html += '</div>';

    return html;
  },

  getQuantityForDiscount: function (productId, productSelections) {
    var quantity = 0;

    // iOS and macOS product SKUs should reflect their combined quantities.
    // This appropriately reflects the quantities used for determining discounts.
    if (productId === JP.PRODUCTS.MACOS.id || productId === JP.PRODUCTS.IOS.id) {
      quantity = JP.getQuantity(JP.PRODUCTS.MACOS.id, productSelections) + JP.getQuantity(JP.PRODUCTS.IOS.id, productSelections);
    } else {
      quantity = JP.getQuantity(productId, productSelections);
    }
    return quantity;
  },

  // Legacy pricing scheme
  // Returns an array of HTML strings.
  // The first HTML string is the row for the product's main SKU.
  // The second HTML string is the row for the product's renewal or support SKU.
  getRowsForLegacyProduct: function (productId, productSelections) {
    var productPricing = JP.getProductPricingFromPriceList(productId, productSelections.support, JP.config.vertical, productSelections.license);
    var quantity = JP.getQuantity(productId, productSelections);
    var quantityForDiscount = this.getQuantityForDiscount(productId, productSelections);
    var quantityIndex = JP.getDiscountIndexForQuantity(quantityForDiscount);
    var sku = productPricing['sku'][quantityIndex];

    // Renewal/support SKU's are optional – when relevant they get stuffed in with main sku, eg:
    // 121-001-1201 + 3x 121-003-1201  (the second SKU is renewal or support)
    // We only care about 2 and 3 year variants.  (1 year does not require any additional rows.)
    var hasEmbeddedOneYearSupportSku = sku.indexOf('+ 1x') !== -1;
    var hasEmbeddedTwoYearSupportSku = sku.indexOf('+ 2x') !== -1;
    var hasEmbeddedThreeYearSupportSku = sku.indexOf('+ 3x') !== -1;
    var qtyRange = (JP.PRODUCT_QUANTITY_BREAKPOINTS[quantityIndex - 1] || 0) + '–' + (JP.PRODUCT_QUANTITY_BREAKPOINTS[quantityIndex] - 1);
    var mainSkuRowHTML = '';
    var productPrefix = this.getProductPrefix(productId);
    var firstCell = quantity + ' &times; ' + productPrefix + JP.getProductDisplayName(productId) + (productId === JP.PRODUCTS.JAMF_NOW.id ? '' : ' (' + qtyRange + ')');
    var embeddedSupportSku;
    var rows = {
      mainBoxRows: [],
      secondaryBoxRows: []
    };

    if (hasEmbeddedOneYearSupportSku || hasEmbeddedTwoYearSupportSku || hasEmbeddedThreeYearSupportSku) {
      var split = sku.split(' ');
      sku = split[0];
      embeddedSupportSku = split[split.length - 1];

      // One year of support embedded; the sku applies to the first year
      if (hasEmbeddedOneYearSupportSku) {
        rows.mainBoxRows.push(this.getRowHtmlString(firstCell + ' – ' + i18n.properties.pricing.quote.first_year_support, embeddedSupportSku));
      }

      // Two years of support embedded; the skus apply to the second and third years
      if (hasEmbeddedTwoYearSupportSku) {
        rows.secondaryBoxRows.push(this.getRowHtmlString(firstCell + ' – ' + i18n.properties.pricing.quote.second_year_support, embeddedSupportSku));
        rows.secondaryBoxRows.push(this.getRowHtmlString(firstCell + ' – ' + i18n.properties.pricing.quote.third_year_support, embeddedSupportSku));
      }

      // Three years support embedded; the skus apply to all three years
      if (hasEmbeddedThreeYearSupportSku) {
        rows.mainBoxRows.push(this.getRowHtmlString(firstCell + ' – ' + i18n.properties.pricing.quote.first_year_support, embeddedSupportSku));
        rows.secondaryBoxRows.push(this.getRowHtmlString(firstCell + ' – ' + i18n.properties.pricing.quote.second_year_support, embeddedSupportSku));
        rows.secondaryBoxRows.push(this.getRowHtmlString(firstCell + ' – ' + i18n.properties.pricing.quote.third_year_support, embeddedSupportSku));
      }
    }

    // 1 Year applied to legacy 1 year subscription products
    if (productSelections.license === 'subscription' && productSelections.support === '1_year') {
      firstCell += ' – 1 Year';
    }

    // 2 Year applied to legacy 2 year subscription products
    if (productSelections.license === 'subscription' && productSelections.support === '2_year') {
      firstCell += ' – 2 Year';
    }

    // 3 Year applied to legacy 3 year subscription products (where one SKU represents 3 years of subscription)
    if (productSelections.license === 'subscription' && productSelections.support === '3_year') {
      firstCell += ' – 3 Year';
    }

    // Main SKU, make sure this row goes first
    mainSkuRowHTML += this.getRowHtmlString(firstCell, sku);
    rows.mainBoxRows.unshift(mainSkuRowHTML);
    return rows;
  },

  // New pricing scheme -- Applies only to macos and ios in apple + edu + subscription
  getRowsForNewProduct: function (productId, productSelections) {

    var pricing = JP.getNewProductPricingFromPriceList(productId, JP.config.vertical, productSelections.support);
    var quantity = JP.getQuantity(productId, productSelections);
    var quantityForDiscount = this.getQuantityForDiscount(productId, productSelections);
    var sku = pricing.sku[0];
    var label = ' &times; Seats for ';
    var yearLabel = productSelections.support === '1_year' ? i18n.properties.pricing.quote.first_year : 'First Three Years';
    var rows = {
      mainBoxRows: [],
      secondaryBoxRows: []
    };

    if (quantity > 0) {
      // Use 10k+ info if relevant
      if (quantityForDiscount >= 10000) {
        sku = pricing.sku[1];
        label = ' &times; Seats (10k+) for ';
      }
      rows.mainBoxRows.push(this.getRowHtmlString(quantity + label + JP.getProductDisplayName(productId) + ' – ' + yearLabel, sku));
    }
    return rows;
  },

  getProductPrefix: function (productId) {
    // For Jamf Now, add no prefix
    if (productId === JP.PRODUCTS.JAMF_NOW.id) {
      return '';
    }

    // For Jamf Pro, add appropriate verbiage
    return [JP.PRODUCTS.IOS.id, JP.PRODUCTS.MACOS.id].indexOf(productId) !== -1 ? 'Jamf Pro for ' : 'Jamf Pro – ';
  },

  getRowForPremiumSupport: function (productSelections) {
    var row = '';
    var pricing;
    var sku;
    var formattedName;
    var productId;

    _.forEach(['premium_support', 'premium_support_plus', 'premium_support_enterprise'], function (supportProductId) {
      if (productSelections[supportProductId]) {
        productId = supportProductId;
        return false;
      }
    });

    if (productId) {
      pricing = _.find(JP.currentPriceList.products, {product: productId});
      sku = pricing[JP.config.distributor === 'apple' ? 'apple_sku' : 'sku'];
      formattedName = JP.convertProductIdToDisplayName(productId);
      row = this.getRowHtmlString('1 &times; ' + formattedName + ' – 1 Year', sku);
    }
    return row;
  },

  getRowForPremiumService: function (productSelections) {
    var row = '';
    var pricing;
    var sku;
    var formattedName;
    var productId;

    _.forEach(['premium_service_silver', 'premium_service_gold', 'premium_service_platinum'], function (serviceProductId) {
      if (productSelections[serviceProductId]) {
        productId = serviceProductId;
        return false;
      }
    });

    if (productId) {
      pricing = _.find(JP.currentPriceList.products, {product: productId});
      sku = pricing[JP.config.distributor === 'apple' ? 'apple_sku' : 'sku'];
      formattedName = JP.convertProductIdToDisplayName(productId);
      row = this.getRowHtmlString('1 &times; ' + formattedName + ' – 1 Year', sku);
    }
    return row;
  },

  getRowForJumpStart: function (productSelections) {
    var row = '';
    var pricing;
    var sku;
    var formattedName;

    if (JP.getQuantity('jumpstart', productSelections) > 0) {
      pricing = _.find(JP.currentPriceList.products, {
        product: 'jumpstart',
        type: productSelections.jumpstart.state.value
      });
      sku = pricing[JP.config.distributor === 'apple' ? 'apple_sku' : 'sku'];
      formattedName = 'Jumpstart – ' + productSelections.jumpstart.state.name;
      row = this.getRowHtmlString('1 &times; ' + formattedName, sku);
    }
    return row;
  },

  getRowsForTrainingPass: function (productSelections) {
    var row = '';
    _.each(['training_pass_individual', 'training_pass_institutional'], function (productId) {
      var pricing = _.find(JP.currentPriceList.products, {product: productId});
      var sku;
      var formattedName;

      if (JP.getQuantity(productId, productSelections) > 0) {
        sku = pricing[JP.config.distributor === 'apple' ? 'apple_sku' : 'sku'];
        formattedName = JP.convertProductIdToDisplayName(productId);
        row += JP.quoteDetails.getRowHtmlString('1 &times; ' + formattedName + ' – 1 Year', sku);
      }
    });
    return row;
  },

  getRowHtmlString: function (cellOneHtmlString, cellTwoHtmlString) {
    return '<tr><td>' + cellOneHtmlString + '</td><td>' + cellTwoHtmlString + '</td></tr>';
  }
};


