JP.getUnitCost = function (productId, quantityForDiscount, vertical, license, support, productPricing, discountIndex) {
  var unitCost;
  // Special case:  Full retail for 3_year / close is 3 * 1_year subscription
  // Does not apply to SCCM plugin
  if (productId !== JP.PRODUCTS.SCCM_PLUGIN.id && productId !== JP.PRODUCTS.JAMF_NOW.id && !quantityForDiscount && support === "3_year" && license === "subscription") {

    // Some editions piggyback on other distributors for pricing, per "distributorForPricing"
    var productKey = JP.config.distributorForPricing + "_" + vertical;
    var oneYearPricing = _.find(JP.currentPriceList.products, {
      "product": productId,
      "license": license,
      "support": "1_year"
    })[productKey];
    unitCost = oneYearPricing.prices[discountIndex] * 3;
  } else {
    unitCost = productPricing.prices[discountIndex];
  }

  if (typeof unitCost !== 'number') {
    console.error('Error: unitCost is not a number.', unitCost);
    return 0;
  }
  return unitCost;
};