JP.QuantityStepper = function () {
  return React.createClass({
    displayName: 'QuantityStepper',

    minusClicked: function (event) {
      this.props.quantityChanged(this.props.enforceQuantityBounds(this.props.quantity - this.props.step));
      this.props.toggleProduct(true);
      event.stopPropagation();
    },

    plusClicked: function (event) {
      this.props.quantityChanged(this.props.enforceQuantityBounds(this.props.quantity + this.props.step));
      this.props.toggleProduct(true);
      event.stopPropagation();
    },

    inputClicked: function (event) {
      this.props.toggleProduct(true);
      event.stopPropagation();
    },

    // Don't enforce min/max on change event, use blur instead!
    // In react, onChange is fired on every keyPress, so intermediate values can trigger premature enforcement
    inputChanged: function (event) {
      var parsedVal = parseInt(event.target.value);

      // parseInt returns NaN when empty or non-number input makes it through
      if (isNaN(parsedVal) || parsedVal < 0) parsedVal = 0;

      this.props.quantityChanged(parsedVal);
      this.props.toggleProduct(true);
    },

    // Only enforce quantity bounds on blur
    inputBlurred: function (event) {
      var parsedVal = parseInt(event.target.value);

      // parseInt returns NaN when empty or non-number input makes it through
      if (isNaN(parsedVal)) parsedVal = 0;

      parsedVal = this.props.enforceQuantityBounds(parsedVal);

      // Only trigger change if value has changed
      if (this.props.quantity !== parsedVal){
        this.props.quantityChanged(parsedVal);
      }
    },

    render: function () {
      return (
        <div>
          <input type="number" value={this.props.quantity} min="0" max="99999" step={this.props.step}
                 onChange={this.inputChanged}
                 onBlur={this.inputBlurred}
                 onClick={this.inputClicked}/>
          <div className="stepper">
            <a className="stepper-minus" onClick={this.minusClicked}>
              <i></i>
            </a>
            <a className="stepper-plus" onClick={this.plusClicked}>
              <i></i>
            </a>
          </div>
        </div>
      )
    }
  });
};;
