  var ConnectTableRowLoadMore = React.createClass({
    // Trigger parent handler
    handleClick: function () {
      this.props.handleLoadMoreClick();
    },

    render: function () {
      return <tr>
        <td className="load-more" colSpan={this.props.numColumns}>
          <button className='btn btn-block' onClick={this.handleClick}>Load more events</button>
        </td>
      </tr>
    }
  });

  var ConnectShareCheckboxButton = React.createClass({
    handleClick: function () {
      this.props.handleShareCheckboxClick(this.props.event);
    },
    render: function () {
      return <button type="button" className="connect-button connect-check-mark-button"
                     title="Select this event to share"
                     onClick={this.handleClick}>
        <span className="connect-check-mark"></span>
      </button>
    }
  });

  var ConnectShareCheckbox = React.createClass({

    isIOS: function () {
      // Adapted from http://stackoverflow.com/questions/9038625/detect-if-device-is-ios
      return /iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream;
    },

    appendMsg: function (msg, msgArray) {
      if (typeof msg === 'string' && msg.length) {
        msgArray.push(msg);
      }
    },

    getPlainTextBody: function (e) {
      var bodyMsgs = [];
      var intro = [e.topic, e.type, e.date + ' at ' + e.time];
      intro = intro.join('\n');
      this.appendMsg(intro, bodyMsgs);
      this.appendMsg(e.url && e.url.length ? 'Details: ' + e.url : '', bodyMsgs);
      this.appendMsg(e.message, bodyMsgs);
      this.appendMsg(e.details, bodyMsgs);
      this.appendMsg('Add this event to your calendar: ' + e.ical_url, bodyMsgs);
      return encodeURIComponent(bodyMsgs.join('\n\n'));
    },

    getHtmlBody: function (e) {
      var bodyMsgs = [];
      var intro = ['<b>' + e.topic + '</b>', e.type, e.date + ' at ' + e.time];
      intro = intro.join('\n');
      this.appendMsg(intro, bodyMsgs);
      this.appendMsg(e.url && e.url.length ? 'Details: <a href="' + e.url + '">' + e.url + '</a>' : '', bodyMsgs);
      this.appendMsg(e.message, bodyMsgs);
      this.appendMsg(e.details, bodyMsgs);
      this.appendMsg('<i>Add this event to your calendar:</i>:\n' + '<a href="' + e.ical_url + '">' + e.ical_url + '</a>', bodyMsgs);
      return encodeURIComponent(bodyMsgs.join('\n\n'));
    },

    getCC: function (event) {
      var cc = 'apple.retail@jamf.com';
      return cc;
    },

    render: function () {
      var e = this.props.event;
      var subject = encodeURIComponent('Jamf Event: ' + e.type + ' on ' + e.date + ' at ' + e.time);
      var body = this.isIOS() ? this.getHtmlBody(e) : this.getPlainTextBody(e);
      var cc = this.getCC(e);
      var href = 'mailto:?subject=' + subject + '&cc=' + cc + '&body=' + body;
      return <a className="connect-button" href={href} target="_blank"><svg xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid" width="32" height="22" viewBox="0 0 32 22"><path d="M29 22H3c-1.654 0-3-1.346-3-3V3c0-1.654 1.346-3 3-3h26c1.654 0 3 1.346 3 3v16c0 1.654-1.346 3-3 3zM3 20h26c.551 0 1-.449 1-1V3.317L16.651 14.759a1.002 1.002 0 0 1-1.302 0L2 3.317V19c0 .551.449 1 1 1zM28.463 2H3.537L16 12.683 28.463 2z"/></svg></a>
    }
  });

// Check for password in search parameters (expecting '?j=secret')
// Solicit from user if not present
  var ConnectAccessWrapper = React.createClass({
    checkSecret: function (secret) {
      return parseInt(secret) === 1984;
    },

    getInitialState: function () {
      var searchParams = new URLSearchParams(location.search);
      var secret = searchParams.get('j');
      var disablePasscode = viewConfig.options.disablePasscode;

      return {
        authenticated: disablePasscode || this.checkSecret(secret),
        inputText: '',
        attemptFailed: false
      }
    },

    handleChange: function (event) {
      var val = event.target.value;
      if (val.length === 4) {
        var authenticated = this.checkSecret(val);
      }

      this.setState({
        inputText: val,
        authenticated: authenticated,
        attemptFailed: !authenticated
      })
    },

    render: function () {
      var codeMessage = this.state.attemptFailed ? <p>Sorry, that's not the code, please try again:</p> :
        <p>{i18n.properties.connect.unlock}</p>;
      var codeInput = <div className="connect-login">
        {codeMessage}
        <input type="tel" ref="codeInput" placeholder="" maxLength="4" onChange={this.handleChange}/>
      </div>
      var connectEvents = <ConnectEvents></ConnectEvents>;
      return this.state.authenticated ? connectEvents : codeInput;
    }
  });

  ReactDOM.render(
    <ConnectAccessWrapper/>,
    section.get(0)
  );
